import React, { useEffect, useState } from 'react';
import './Set.css';

const Set = () => {
  const [user, setUser] = useState(null);

  useEffect(() => {
    const tg = window.Telegram?.WebApp;
    if (tg?.initDataUnsafe?.user) {
      setUser(tg.initDataUnsafe.user);
    }
  }, []);

  return (
    <>
      {/* Блок профиля с данными Telegram */}
      <div className="settings-container profile-header">
        <img
          src={user?.photo_url || 'https://s3.pxsto.re/static/white.png'}
          alt="Аватар"
          className="profile-avatar"
        />
        <div className="profile-info">
          <span className="profile-name">
            {user?.first_name || 'Мой король'}
            {user?.last_name ? ' ' + user.last_name : ''}
          </span>
          <span className="profile-role">
            {user?.username ? '@' + user.username : 'Пользователь'}
          </span>
        </div>

        {/* ЗАМЕНЁННЫЙ ЭЛЕМЕНТ */}
        <button
          className="profile-button"
          onClick={() => alert('Переход в Личный кабинет')}
        >
          Личный кабинет
        </button>
      </div>

      {/* Основные настройки */}
      <div className="settings-container">
        <h3 className="settings-section-title">Основные настройки</h3>
        <div className="settings-card">
          <button className="settings-button">
            <div className="settings-button-content">
              <i className="ri-earth-fill settings-icon"></i>
              Язык приложения
            </div>
            <i className="ri-arrow-drop-right-line arrow-icon"></i>
          </button>
          <button className="settings-button">
            <div className="settings-button-content">
              <i className="ri-palette-fill settings-icon"></i>
              Тема приложения
            </div>
            <i className="ri-arrow-drop-right-line arrow-icon"></i>
          </button>
          <button className="settings-button">
            <div className="settings-button-content">
              <i className="ri-calendar-event-fill settings-icon"></i>
              Начало недели
            </div>
            <i className="ri-arrow-drop-right-line arrow-icon"></i>
          </button>
          <button className="settings-button">
            <div className="settings-button-content">
              <i className="ri-time-fill settings-icon"></i>
              Формат времени
            </div>
            <i className="ri-arrow-drop-right-line arrow-icon"></i>
          </button>
        </div>
      </div>

      {/* Связь с нами */}
      <div className="settings-container">
        <h3 className="settings-section-title">Связь с нами</h3>
        <div className="settings-card">
          <button className="settings-button">
            <div className="settings-button-content">
              <i className="ri-team-fill settings-icon"></i>
              Сотрудничество
            </div>
            <i className="ri-arrow-drop-right-line arrow-icon"></i>
          </button>
          <button className="settings-button">
            <div className="settings-button-content">
              <i className="ri-telegram-fill settings-icon"></i>
              Наш Telegram-канал
            </div>
            <i className="ri-arrow-drop-right-line arrow-icon"></i>
          </button>
          <button className="settings-button">
            <div className="settings-button-content">
              <i className="ri-error-warning-fill settings-icon"></i>
              Тех. поддержка
            </div>
            <i className="ri-arrow-drop-right-line arrow-icon"></i>
          </button>
        </div>
      </div>

      {/* Благодарности */}
      <div className="credits-final">
        <div className="credits-line">
          <span className="credits-tag">Разработка:</span>
          <i className="ri-code-s-slash-line"></i> rusoy,
          <i className="ri-code-s-slash-line"></i> luulithh
        </div>
        <div className="credits-line">
          <span className="credits-tag">Тестирование:</span>
          Eran_m,
          Alena Resh,
          Ryota89,
          AnnZ14ka
        </div>
      </div>
    </>
  );
};

export default Set;
